
import Scroller from 'vux/src/components/scroller/index.vue'

import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
name: "imgmore",
props: ["aid", "bid"],
data: function() {
return {
imgmore: {},
page: 1,
pullupConfig: {
content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中……"
}
};
},
components: {
Scroller,
Grid,
GridItem
},
methods: {

loadMoreData: function() {
var app = this;
this.page = this.page + 1;
console.log(this.page);
this.$http
.get(
"/v2-car-getCategoryImageList.html?SerialID=" +
this.aid +
"&ImageID=" +
this.bid +
"&Page=" +
this.page +
"&PageSize=30&_1531054352382"
)
.then(function(response) {
console.log(response.data.data);

for (var i = 0; i < response.data.data.List.length; i++) {
app.imgmore.List.push(response.data.data.List[i]);
}
app.$refs.pullup.donePullup();
});
}
},

beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get(
"/v2-car-getCategoryImageList.html?SerialID=" +
to.params.aid +
"&ImageID=" +
to.params.bid +
"&Page=" +
vm.page +
"&PageSize=30&_1531054352382"
)
.then(function(response) {
console.log(response.data.data);
vm.imgmore = response.data.data;
});
});
}
};



