import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home.vue'
import Xqy from '@/components/Xqy.vue'
import Imga from '@/components/Imga.vue'
import User from '@/components/User.vue'
import Imgs from '@/components/Imgs.vue'
Vue.use(VueRouter)

const routes=[
 
   {path:'/',name:'home',component:Home,props:true},
   {path:'/xqy/:id',name:'xqy',component:Xqy,props:true},
   {path:'/imga/:id',name:'imga',component:Imga,props:true},
   {path:'/user/:id',name:'user',component:User,props:true},
   {path:'/imgs/:aid/:bid',name:'imgs',component:Imgs,props:true},

 ];
 //配置路由管理器
 const router = new VueRouter({
   routes
 });
 
 export default router;



