
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
name: "imga",
components: {
Grid,
GridItem
},
props: ["id"],
data: function() {
return {
waiguan: {},
neishi: {},
kongjian: {},
tujie: {},
guanfang: {}
};
},
methods: {
imgs(aid, bid,cid) {
this.$router.push("/imgs/" + aid +"/"+ bid );
}
},

beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get("/v2-car-getImageList.html?SerialID=" + to.params.id)
.then(function(response) {

vm.waiguan = response.data.data[0];
vm.neishi = response.data.data[1];
vm.kongjian = response.data.data[2];
vm.tujie = response.data.data[3];
vm.guanfang = response.data.data[4];

});
});
}
};



